"use client"

import * as React from "react"
import { Loader2 } from "lucide-react"
import { useRouter } from "next/navigation"
import { toast } from "sonner"

import { useAuth } from "@/features/auth"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import {
  Field,
  FieldDescription,
  FieldGroup,
  FieldLabel,
} from "@/components/ui/field"
import { Input } from "@/components/ui/input"

export function LoginForm({
  className,
  ...props
}) {
  const router = useRouter()
  const { user, login, hydrated } = useAuth()
  const [email, setEmail] = React.useState("")
  const [name, setName] = React.useState("")
  const [isSubmitting, setIsSubmitting] = React.useState(false)

  React.useEffect(() => {
    if (hydrated && user) {
      router.replace("/")
    }
  }, [hydrated, user, router])

  const handleSubmit = React.useCallback(async (event) => {
    event?.preventDefault()
    setIsSubmitting(true)

    try {
      const authenticatedUser = await login({
        provider: "openid-connect",
        email,
        name,
      })

      const greetingName =
        authenticatedUser.name || authenticatedUser.email || "사용자"
      toast.success(`${greetingName}님, 환영합니다!`)
      router.replace("/")
    } catch (error) {
      const message =
        error instanceof Error ? error.message : "알 수 없는 오류가 발생했습니다."
      toast.error(message)
    } finally {
      setIsSubmitting(false)
    }
  }, [email, login, name, router])

  return (
    <div className={cn("flex flex-col gap-6", className)} {...props}>
      <Card>
        <CardHeader>
          <CardTitle>OpenID SSO 로그인</CardTitle>
          <CardDescription>
            사내 OpenID 공급자를 통해 단 한 번의 클릭으로 로그인하세요.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit}>
            <FieldGroup>
              <Field>
                <FieldLabel htmlFor="email">회사 이메일</FieldLabel>
                <Input
                  id="email"
                  type="email"
                  placeholder="m@example.com"
                  value={email}
                  onChange={(event) => setEmail(event.target.value)}
                  autoComplete="email"
                  required
                />
              </Field>
              <Field>
                <FieldLabel htmlFor="name">표시 이름 (선택)</FieldLabel>
                <Input
                  id="name"
                  type="text"
                  placeholder="홍길동"
                  value={name}
                  onChange={(event) => setName(event.target.value)}
                  autoComplete="name"
                />
                <FieldDescription>
                  실제 OpenID 공급자와 연동 시 프로필에서 자동으로 수신됩니다.
                </FieldDescription>
              </Field>
              <Field>
                <Button type="submit" disabled={isSubmitting}>
                  {isSubmitting ? (
                    <>
                      <Loader2 className="mr-2 size-4 animate-spin" aria-hidden="true" />
                      로그인 중...
                    </>
                  ) : (
                    "OpenID SSO로 계속"
                  )}
                </Button>
                <FieldDescription className="text-center">
                  현재는 데모 환경으로 즉시 로그인되며,<br />
                  폐쇄망 이전 시 API 엔드포인트만 교체하면 됩니다.
                </FieldDescription>
              </Field>
            </FieldGroup>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}
